import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from "../../common/context/AuthContext";
import { useUnifiedToast } from "../../common/services/unifiedToastService";
import {
  UnifiedBackground,
  UnifiedCard,
  useScrollAnimation,
  SharedStyles
} from "../../common/components/SharedTheme";
import { SkeletonFullPage } from '../components/skeletons';
import { supabase } from "../../common/services/supabaseClient"
import logger from '../../common/utils/logger';
import {
  Users,
  UserCheck,
  CreditCard,
  Pin,
  RefreshCw,
  Award,
  Gift,
  DollarSign,
  Target,
  FileText,
  Banknote,
  ArrowUpRight
} from 'lucide-react';

function AdminDashboard() {
  const { user } = useAuth();
  const { showSuccess, showError } = useUnifiedToast();
  const navigate = useNavigate();
  useScrollAnimation();

  const [dashboardStats, setDashboardStats] = useState({
    totalPromoters: 0,
    activePromoters: 0,
    totalCustomers: 0,
    totalRevenue: 0,
    totalPins: 0,
    usedPins: 0,
    activeWithdrawals: 0,
    pendingWithdrawals: 0,
    totalCommissions: 0,
    pendingCommissions: 0,
    pendingKYC: 0,
    batch1Count: 0,
    batch2Count: 0,
  });
  const [loading, setLoading] = useState(true);

  const loadDashboardData = useCallback(async () => {
    const startTime = performance.now();
    try {
      logger.info('Loading admin dashboard data', { userId: user?.id });
      setLoading(true);

      const [promotersResult, customersResult] = await Promise.all([
        supabase.from('profiles').select('id, status').eq('role', 'promoter'),
        supabase.from('profiles').select('id').eq('role', 'customer')
      ]);

      const totalPromoters = promotersResult.data?.length || 0;
      const activePromoters = promotersResult.data?.filter(p => p.status === 'Active' || p.status === 'active').length || 0;
      const totalCustomers = customersResult.data?.length || 0;

      const stats = {
        totalPromoters,
        activePromoters,
        totalCustomers,
        totalRevenue: 0,
        totalPins: 0,
        usedPins: 0,
        activeWithdrawals: 0,
        pendingWithdrawals: 0,
        totalCommissions: 0,
        pendingCommissions: 0,
        pendingKYC: 0
      };

      setDashboardStats(stats);

      setTimeout(async () => {
        try {
          const [pinsResult, withdrawalsResult, commissionsResult, kycResult, batch1Result, batch2Result] = await Promise.all([
            supabase.rpc('get_pin_stats'),
            supabase.rpc('get_withdrawal_stats'),
            supabase.rpc('get_commission_stats'),
            supabase.from('kyc_submissions').select('id', { count: 'exact', head: true }).eq('status', 'Pending'),
            supabase.from('profiles').select('id', { count: 'exact', head: true }).eq('role', 'customer').or('batch.eq.Batch 1,batch.is.null'),
            supabase.from('profiles').select('id', { count: 'exact', head: true }).eq('role', 'customer').eq('batch', 'Batch 2')
          ]);

          setDashboardStats(prev => ({
            ...prev,
            totalRevenue: commissionsResult.data?.total_revenue || 0,
            totalPins: pinsResult.data?.total_pins || 0,
            usedPins: pinsResult.data?.used_pins || 0,
            activeWithdrawals: withdrawalsResult.data?.active_withdrawals || 0,
            pendingWithdrawals: withdrawalsResult.data?.pending_withdrawals || 0,
            totalCommissions: commissionsResult.data?.total_commissions || 0,
            pendingCommissions: commissionsResult.data?.pending_commissions || 0,
            pendingKYC: kycResult.count || 0,
            batch1Count: batch1Result.count || 0,
            batch2Count: batch2Result.count || 0
          }));
        } catch (error) {
          logger.error('Error loading background dashboard data', { error, userId: user?.id });
        }
      }, 300);

    } catch (error) {
      logger.error('Error loading dashboard data', { error, userId: user?.id });
      showError('Failed to load dashboard data');
    } finally {
      setLoading(false);
    }
  }, [user?.id, showError]);

  useEffect(() => {
    if (user?.id) {
      loadDashboardData();
      const interval = setInterval(loadDashboardData, 5 * 60 * 1000);
      return () => clearInterval(interval);
    }
  }, [user, loadDashboardData]);

  const handleCardClick = (route) => {
    navigate(route);
  };

  if (loading) {
    return (
      <>
        <SharedStyles />
        <UnifiedBackground minHeight="min-h-screen">
          <SkeletonFullPage type="dashboard" />
        </UnifiedBackground>
      </>
    );
  }

  return (
    <>
      <style>{`
        @keyframes fadeInUp {
          from { opacity: 0; transform: translateY(30px); }
          to { opacity: 1; transform: translateY(0); }
        }
        [data-animate] { animation: fadeInUp 0.6s ease-out; }
      `}</style>
      <SharedStyles />
      <UnifiedBackground minHeight="min-h-screen" className="flex flex-col">
        <div className="relative flex-1">
          <div className="absolute inset-0 overflow-hidden pointer-events-none">
            <div className="absolute inset-0 bg-gradient-to-br from-orange-500/5 via-transparent to-yellow-500/5"></div>
            <div className="absolute top-0 left-0 w-96 h-96 bg-orange-400/5 rounded-full filter blur-3xl animate-pulse"></div>
          </div>

          <div className="relative max-w-6xl mx-auto px-6 pt-36 pb-8">
            <div className="mb-6" data-animate>
              <div className="flex flex-col space-y-0.5">
                <div className="flex items-center space-x-3">
                  <span className="px-2 py-0.5 bg-orange-500/10 border border-orange-500/20 rounded text-orange-400 text-[9px] font-black uppercase tracking-widest">System Online</span>
                  <div className="h-px flex-1 bg-slate-800/30"></div>
                </div>
                <div className="flex items-center justify-between mt-2">
                  <div>
                    <h2 className="text-slate-500 text-[10px] font-bold uppercase tracking-[0.2em]">Administrator Dashboard</h2>
                    <h1 className="text-3xl font-black text-white tracking-tighter">Command Center</h1>
                  </div>
                  <button
                    onClick={loadDashboardData}
                    className="p-2.5 bg-slate-800/40 hover:bg-slate-700/60 border border-slate-700/50 rounded-xl transition-all group shadow-lg"
                    title="Refresh Live Metrics"
                  >
                    <RefreshCw className="w-4 h-4 text-orange-400 group-hover:rotate-180 transition-transform duration-700" />
                  </button>
                </div>
              </div>
            </div>

            <div className="grid grid-cols-2 lg:grid-cols-4 gap-4 mb-8" data-animate>
              {[
                { label: 'Total Promoters', value: dashboardStats.totalPromoters, color: 'text-orange-400', border: 'hover:border-orange-500/50', icon: Users, route: '/admin/promoters' },
                { label: 'Total Customers', value: dashboardStats.totalCustomers, color: 'text-blue-400', border: 'hover:border-blue-500/50', icon: UserCheck, route: '/admin/customers' },
                { label: 'Batch 1', value: dashboardStats.batch1Count, color: 'text-emerald-400', border: 'hover:border-emerald-500/50', icon: Award, route: '/admin/customers?batch=Batch 1' },
                { label: 'Batch 2', value: dashboardStats.batch2Count, color: 'text-purple-400', border: 'hover:border-purple-500/50', icon: Gift, route: '/admin/customers?batch=Batch 2' },
              ].map((stat, i) => (
                <UnifiedCard
                  key={i}
                  className={`group relative overflow-hidden p-6 border border-slate-700/40 ${stat.border} bg-slate-900/60 backdrop-blur-xl transition-all duration-300 transform hover:-translate-y-1`}
                  onClick={() => handleCardClick(stat.route)}
                >
                  <div className="relative z-10 flex flex-col">
                    <span className={`${stat.color} text-[9px] font-black uppercase tracking-widest mb-1 opacity-80`}>{stat.label}</span>
                    <div className="flex items-baseline space-x-2">
                      <span className="text-3xl font-black text-white tracking-tighter">{stat.value}</span>
                      <div className="w-1.5 h-1.5 rounded-full bg-emerald-500/50 animate-pulse" />
                    </div>
                  </div>
                  <stat.icon className={`absolute -right-2 -bottom-2 w-16 h-16 ${stat.color} opacity-[0.02] group-hover:opacity-[0.06] transition-all duration-500 transform group-hover:scale-110`} />
                </UnifiedCard>
              ))}
            </div>

            <div className="mt-6" data-animate>
              <div className="flex items-center space-x-3 mb-4">
                <div className="w-1 h-6 bg-gradient-to-b from-orange-500 to-red-600 rounded-full"></div>
                <h2 className="text-lg font-black text-white tracking-tight uppercase">Administrative Tools</h2>
              </div>
              <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                {[
                  { label: 'Promoters', icon: Users, route: '/admin/promoters', color: 'orange' },
                  { label: 'Customers', icon: UserCheck, route: '/admin/customers', color: 'blue' },
                  { label: 'Pin Mgmt', icon: Pin, route: '/admin/pins', color: 'purple' },
                  { label: 'KYC Sync', icon: FileText, route: '/admin/approval?tab=kyc', color: 'yellow' },
                  { label: 'Bank Approvals', icon: Banknote, route: '/admin/approval?tab=bank', color: 'emerald' },
                  { label: 'Income', icon: DollarSign, route: '/admin/income', color: 'emerald' },
                  { label: 'Withdrawal', icon: CreditCard, route: '/admin/withdrawals', color: 'green' },
                  { label: 'Repayment', icon: Target, route: '/admin/repayments', color: 'indigo' },
                ].map((tool, i) => (
                  <div
                    key={i}
                    className="group cursor-pointer transition-all duration-300 border border-slate-700/40 hover:border-orange-500/40 bg-slate-900/60 p-5 rounded-3xl hover:shadow-xl hover:shadow-orange-500/5 flex flex-col items-center text-center space-y-3"
                    onClick={() => navigate(tool.route)}
                  >
                    <div className="w-12 h-12 bg-orange-500/10 rounded-xl flex items-center justify-center group-hover:bg-orange-500 group-hover:rotate-3 transition-all duration-300 shadow-inner">
                      <tool.icon className="w-6 h-6 text-orange-400 group-hover:text-white transition-colors duration-300" />
                    </div>
                    <h3 className="text-[10px] font-black text-white tracking-wider uppercase opacity-80 group-hover:opacity-100">{tool.label}</h3>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </UnifiedBackground>
    </>
  );
}

export default AdminDashboard;
